html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
  background-color: #efebeb;
  color: #000000;
  font-size: 20px;
  font-family: "PT Serif", serif;
  overflow: hidden;
}
.primary-title {
  font-size: 100px;
  font-weight: 700;
  line-height: 90px;
}
.secondary-title {
  font-size: 70px;
  font-weight: 700;
  line-height: 68px;
}
.main-text {
  font-family: "Proxima Nova", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 32px;
}
.main-button {
  text-decoration: none;
  cursor: pointer;
  background-color: #000000;
  font-size: 18px;
  padding: 13px 21px;
  padding-right: 13px;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  min-width: 235px;
  border-radius: 18px;
  color: #fbbf28;
  border: none;
}
.main-button > span {
  display: inline-block;
  margin-left: 42px;
  width: 16px;
  height: 12px;
  background-image: url(../img/arrow-pointing-to-right.png);
  background-size: contain;
  background-repeat: no-repeat;
}
.quote-wrapper blockquote {
  margin: 0;
}
.quote-text {
  font-size: 32px;
  font-weight: 400;
  line-height: 42px;
}
.quote-author {
  font-weight: 700;
  font-family: "PT Serif", serif;
}
.disclaimer p {
  margin: 0;
  color: #090909;
  font-family: "Proxima Nova", sans-serif;
  font-size: 9px;
  font-weight: 400;
  line-height: 20px;
}
.fp-slidesContainer {
  transition: all 1500ms ease 0s !important;
}
section {
  width: 100vw;
  max-height: 100vh;
}
section .start-page__desc-box {
  -webkit-transform: translateX(-500px);
          transform: translateX(-500px);
  opacity: 0;
  transition: all 1s ease-in-out;
}
section.active .start-page__desc-box {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
  transition: all 1s ease-in-out;
}
section .why-us__desc-wrap {
  -webkit-transform: translateX(800px);
          transform: translateX(800px);
  opacity: 0;
  transition: all 1s ease-in-out;
}
section.active .why-us__desc-wrap {
  -webkit-transform: translateX(50px);
          transform: translateX(50px);
  opacity: 1;
  transition: all 1s ease-in-out;
}
/* pt-serif-regular - latin_cyrillic */
@font-face {
  font-family: 'PT Serif';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('PT Serif'), local('PTSerif-Regular'), url('../fonts/pt-serif-v11-latin_cyrillic-regular.woff2') format('woff2'), url('../fonts/pt-serif-v11-latin_cyrillic-regular.woff') format('woff');
}
/* pt-serif-700 - latin_cyrillic */
@font-face {
  font-family: 'PT Serif';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local('PT Serif Bold'), local('PTSerif-Bold'), url('../fonts/pt-serif-v11-latin_cyrillic-700.woff2') format('woff2'), url('../fonts/pt-serif-v11-latin_cyrillic-700.woff') format('woff');
}
@font-face {
  font-family: 'Proxima nova';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Proxima nova'), local('Proxima-nova'), url('../fonts/ProximaNova-Regular.woff2') format('woff2'), url('../fonts/ProximaNova-Regular.woff') format('woff');
}
.start-page {
  min-height: 100vh;
  position: relative;
}
.start-page::after {
  content: '';
  position: absolute;
  top: -220px;
  bottom: 0;
  left: 64px;
  right: -55%;
  width: 2330px;
  background-image: url(../img/why-us-cloud.png);
  background-repeat: no-repeat;
  z-index: -1;
  background-size: cover;
}
.start-page__header {
  padding-left: 64px;
  padding-top: 42px;
  position: absolute;
  top: 0;
  left: 0;
}
.logo {
  text-decoration: none;
  display: inline-block;
  width: 156px;
  height: 48px;
}
.logo__img {
  width: 100%;
  height: 100%;
  -o-object-fit: contain;
     object-fit: contain;
}
.start-page__desc-box {
  display: flex;
  align-items: center;
  width: 45%;
  margin-left: auto;
  height: 100%;
}
.start-page__desc-wrap {
  max-width: 488px;
  text-align: center;
}
.start-page__slogan {
  font-size: 24px;
  font-weight: 500;
  line-height: 68px;
  margin: 0;
  font-family: "Proxima Nova", sans-serif;
}
.start-page__title {
  margin: 0;
  margin-bottom: 36px;
}
.why-us {
  min-height: 100vh;
  position: relative;
}
.why-us__container {
  display: flex;
  height: 100%;
  align-items: center;
}
.why-us__desc-box {
  display: flex;
  align-items: center;
  height: 100%;
}
.why-us__desc-wrap {
  max-width: 585px;
}
.why-us__title {
  margin: 0;
}
.why-us__cards-wrapper {
  z-index: 9;
  max-width: 50%;
  padding-left: 350px;
}
.why-us__cards-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}
.why-us__card {
  width: 300px;
  height: 250px;
}
.why-us__card:first-child {
  width: 450px;
}
.why-us__card:not(:last-child) {
  margin-right: 30px;
}
.why-us__card-link {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  width: 100%;
  height: 100%;
}
.why-us__card-img {
  -o-object-fit: contain;
     object-fit: contain;
}
.ads {
  min-height: 100vh;
  position: relative;
  display: flex;
  z-index: -1;
}
.ads__quote-wrapper {
  max-width: 660px;
  display: flex;
  align-items: flex-end;
  padding-bottom: 100px;
  margin-left: 110px;
}
.ads__disclaimer {
  position: absolute;
  bottom: 15px;
  left: 110px;
}
.callback {
  min-height: 100vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
}
.callback::after {
  content: '';
  background-image: url(../img/callback.png);
  position: absolute;
  top: -220px;
  bottom: 0;
  left: -440px;
  width: 100vw;
  background-repeat: no-repeat;
  z-index: -1;
  background-size: cover;
}
.callback__fields-wrapper {
  max-width: 720px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 20px;
  grid-column-gap: 20px;
}
.callback__form input {
  width: 348px;
  height: 50px;
  border-radius: 15px;
  background-color: #ffffff;
  color: #000000;
  border: none;
  padding-left: 30px;
  font-size: 18px;
  font-family: "Proxima Nova", sans-serif;
}
.callback__form textarea {
  resize: none;
  width: 348px;
  height: 190px;
  border-radius: 15px;
  border: none;
  padding-left: 30px;
  font-size: 18px;
  font-family: "Proxima Nova", sans-serif;
  color: #000000;
  padding-top: 19px;
}
::-webkit-input-placeholder {
  font-size: 18px;
  color: #000000;
  font-family: "Proxima Nova", sans-serif;
}
::-moz-placeholder {
  font-size: 18px;
  color: #000000;
  font-family: "Proxima Nova", sans-serif;
}
:-moz-placeholder {
  font-size: 18px;
  color: #000000;
  font-family: "Proxima Nova", sans-serif;
}
:-ms-input-placeholder {
  font-size: 18px;
  color: #000000;
  font-family: "Proxima Nova", sans-serif;
}
.form-label--textarea {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 4;
}
.callback__form-submit-button {
  margin-top: 50px;
}
.callback__form-wrapper {
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 150px;
}
.callback__title {
  margin: 0;
  margin-bottom: 53px;
}
.callback__desc-box {
  max-width: 220px;
  margin-right: 70px;
  padding-top: 155px;
}
.callback__footer {
  position: absolute;
  right: 0;
  bottom: 0;
  padding-right: 130px;
  padding-bottom: 57px;
}
@media screen and (max-height: 1000px) {
  .ads__quote-wrapper {
    padding-bottom: 60px;
  }
}
@media screen and (max-height: 899px) {
  .ads__quote-wrapper .quote-text {
    font-size: 24px;
    line-height: 30px;
  }
}
@media screen and (min-height: 950px) {
  .start-page::after {
    width: 150vw;
  }
  .callback::after {
    width: 80vw;
  }
}
@media screen and (min-width: 1279px) and (max-width: 1365px) {
  .primary-title {
    font-size: 80px;
    line-height: 80px;
  }
  .start-page::after {
    width: 195vw;
  }
  .start-page__desc-box {
    width: 40%;
  }
}
@media screen and (min-width: 1279px) and (max-width: 1365px) and (min-height: 1000px) {
  .callback::after {
    width: 110vw;
  }
}
